import React from "react";
import clsx from "clsx";
import * as st from "./markets.module.css";

export const Markets = ({ data, className, style, ...rest }) => {
  return (
    <div {...rest} style={{ ...style }} className={clsx(st.wrapper, className)}>
      {data.map(({ altImg, img: { publicURL }, isVisible, url }) => {
        return (
          isVisible && (
            <a href={url} rel="noreferrer" target="_blank" key={publicURL}>
              <img className={st.logo} src={publicURL} alt={altImg} />
            </a>
          )
        );
      })}
    </div>
  );
};
