import React from "react";
import * as st from "./index.module.css";
import clsx from "clsx";
import { Layout } from "../components/ui/layout";
import { Container } from "../components/ui/container";
import { BikeOnHand } from "../components/bike-on-hand";
import { Triangle } from "../components/triangle";
import { Markets } from "../components/markets";
import { graphql } from "gatsby";

const IndexPage = ({ data }) => {
  const {
    indexPageJson: { markets },
  } = data;

  return (
    <div className={st.indexPage}>
      <Container className={st.container}>
        <div className={clsx(st.pane, st.pane_left)}>
          <div className={st.pane__headingWrapper}>
            <h1 className={st.pane__heading}>
              Производство и продажа оборудования для{" "}
              <span className={st.heading__span}>ремонта</span> /{" "}
              <span className={st.heading__span}>хранения</span> велосипеда
            </h1>
          </div>
          <Triangle width={100} top="10%" left="10%" stroke={4} />
          <Markets data={markets} className={st.pane__markets} />
        </div>
        <div className={clsx(st.pane, st.pane_right)}>
          <BikeOnHand />
          <Triangle width={70} bottom="20%" left="0%" stroke={5} rotated />
          <Triangle width={50} top="2%" right="10%" stroke={7} />
        </div>
      </Container>
    </div>
  );
};

IndexPage.Layout = Layout;
export default IndexPage;

export const query = graphql`
  {
    indexPageJson {
      markets {
        altImg
        name
        url
        img {
          publicURL
        }
        isVisible
      }
    }
  }
`;
