import React from "react";
import clsx from "clsx";
import * as st from "./triangle.module.css";

export const Triangle = ({
  wrapperClassName,
  svgClassName,
  rotated,
  top,
  bottom,
  left,
  right,
  width,
  stroke,
  ...rest
}) => {
  const calculatedWrapperStyle = {
    top,
    bottom,
    left,
    right,
    transform: rotated && "rotate(60deg)",
    maxWidth: width,
  };

  const calculatedSvgStyle = {
    strokeWidth: stroke,
  };

  return (
    <div
      {...rest}
      style={calculatedWrapperStyle}
      className={clsx(st.wrapper, wrapperClassName)}
    >
      <svg
        className={clsx(st.svg, svgClassName)}
        style={calculatedSvgStyle}
        width="100%"
        height="100%"
        viewBox="0 0 70 60"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path d="M6.42116 56.5L35 7L63.5788 56.5H6.42116Z" />
      </svg>
    </div>
  );
};
